<script>
  import Utils from "../utils/utils";

  export default {
    name: "UserLayout",
    data() {
      return {
        user: {}
      }
    },
    mounted() {
      let self = this
      if (!Utils.getLocalUser()) {
        console.log("not signed in")
        return self.$router.push({path: '/login'})
      } else {
        this.user = Utils.getLocalUser();
      }
    },
    methods: {
      logout: function () {
        let self = this
        Utils.removeLocalUser();
        return self.$router.push({path: '/login'})
      }
    }
  }
</script>

